/* Layout chính */
.story-layout {
    width: 100%;
    overflow-x: hidden;
    font-family: 'Georgia', serif;
}

/* HERO */
.hero-section {
    height: 100vh;
    width: 100%;
    background-image: url("images/616447304_1413854823710273_5607893527885689186_n.jpg");
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
}

.hero-overlay {
    background: rgba(0, 0, 0, 0.45);
    padding: 40px 60px;
    border-radius: 12px;
    text-align: center;
    color: white;
}

.hero-overlay h1 {
    font-size: 4rem;
    margin-bottom: 15px;
    font-weight: bold;
    letter-spacing: 3px;
}

.hero-overlay h2 {
    font-size: 1.8rem;
    margin-bottom: 25px;
    font-weight: normal;
    color: #ffd54f;
}

/* Video Button */
.video-btn {
    background: linear-gradient(135deg, #c62828, #e53935);
    color: white;
    border: none;
    padding: 15px 40px;
    font-size: 1.2rem;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(198, 40, 40, 0.4);
    font-family: 'Georgia', serif;
}

.video-btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 20px rgba(198, 40, 40, 0.6);
}

/* Video Modal */
.video-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.85);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.video-modal-box {
    position: relative;
    width: 90%;
    max-width: 900px;
    background: #000;
    border-radius: 16px;
    padding: 10px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.8);
    animation: scaleIn 0.3s ease;
}

@keyframes scaleIn {
    from {
        transform: scale(0.9);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

.video-modal-box video {
    width: 100%;
    border-radius: 12px;
    display: block;
}

.video-modal-close {
    position: absolute;
    top: -45px;
    right: 0;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    color: white;
    font-size: 2rem;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
}

.video-modal-close:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: scale(1.1);
}

/* Legacy classes - keep for compatibility */
.video-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.video-modal-content {
    position: relative;
    width: 90%;
    max-width: 900px;
}

.video-modal-content video {
    width: 100%;
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

.video-close-btn {
    position: absolute;
    top: -40px;
    right: 0;
    background: none;
    border: none;
    color: white;
    font-size: 2.5rem;
    cursor: pointer;
    transition: transform 0.2s;
}

.video-close-btn:hover {
    transform: scale(1.2);
}

/* Video Section (Bottom of page) */
.video-section {
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
    padding: 80px 20px;
    text-align: center;
}

.video-section-content {
    max-width: 900px;
    margin: 0 auto;
}

.video-section h2 {
    color: #ffd54f;
    font-size: 2.5rem;
    margin-bottom: 15px;
    font-family: 'Georgia', serif;
}

.video-section p {
    color: #ccc;
    font-size: 1.1rem;
    margin-bottom: 30px;
}

.video-wrapper {
    margin-top: 20px;
}

.video-wrapper video {
    width: 100%;
    max-width: 800px;
    border-radius: 12px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
}

/* TEXT SECTION */
.text-section {
    position: relative;
    width: 100%;
    padding: 100px 0;
    line-height: 1.8;
    font-size: 1.1rem;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.text-section h2 {
    color: #ffd54f;
    margin-bottom: 20px;
    font-family: 'Georgia', serif;
    line-height: 1.8;
    font-size: 2rem;
    font-weight: bold;
}

/* Ảnh nền FULL WIDTH */
.text-section::before {
    content: "";
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background: url("images/img_1.png") center/cover no-repeat;
    z-index: -2;
}

/* Lớp phủ tối để chữ dễ đọc */
.text-section::after {
    content: "";
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.55);
    z-index: -1;
}

/* Container giới hạn text */
.text-section > * {
    position: relative;
    z-index: 1;
    max-width: 900px;
    width: 100%;
    padding: 0 40px;
}

.text-section h2 {
    color: #ffd54f; /* vàng kim */
    margin-bottom: 20px;
    font-family: 'Georgia', serif;
    line-height: 1.8;
    font-size: 16px;
}

.text-section p {
    margin-bottom: 15px;
}

.text-section strong {
    color: #fff59d;
}

/* Wrapper cho tất cả nội dung text */
.text-section {
    flex-direction: column;
}

/* Alternative background cho section 5 */
.text-section.alt-bg::before {
    background: url("images/img_1.png") center/cover no-repeat;
}

/* IMAGE BREAK (kiểu kể chuyện giống báo điện tử) - FULL WIDTH */
.image-break {
    height: 70vh;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

/* ẢNH RIÊNG */
.img-1 {
    background-image: url("images/ee3d4f8ff111ea55663ad22694f091d4.jpg");
}

.img-2 {
    background-image: url("images/599670673_4378374059149610_1670181223838855279_n.jpg");
}

.img-3 {
    background-image: url("images/616110753_1376573993677044_4990641346874349935_n.jpg");
}

.img-4 {
    background-image: url("images/615903933_1069310418670873_8682039557470043799_n.jpg");
}

.img-5 {
    background-image: url("images/img.png");
}

/* ===== RESPONSIVE ===== */

/* Tablet */
@media (max-width: 1024px) {
    .text-section {
        padding: 80px 0;
        font-size: 1rem;
    }

    .text-section > * {
        max-width: 750px;
        padding: 0 30px;
    }

    .hero-overlay h1 {
        font-size: 3rem;
    }

    .hero-overlay h2 {
        font-size: 1.5rem;
    }

    .hero-overlay {
        padding: 30px 40px;
    }

    .video-btn {
        padding: 12px 30px;
        font-size: 1.1rem;
    }

    .video-section {
        padding: 60px 20px;
    }

    .video-section h2 {
        font-size: 2rem;
    }
}

/* Mobile */
@media (max-width: 768px) {
    .hero-section {
        min-height: 100vh;
        height: auto;
        padding: 60px 15px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .hero-overlay {
        padding: 30px 20px;
        width: 90%;
        max-width: 400px;
        margin: 0 auto;
    }

    .hero-overlay h1 {
        font-size: 2.2rem;
        letter-spacing: 2px;
        margin-bottom: 12px;
    }

    .hero-overlay h2 {
        font-size: 1.1rem;
        margin-bottom: 20px;
        line-height: 1.4;
    }

    .video-btn {
        padding: 12px 28px;
        font-size: 1rem;
    }

    .video-modal-box {
        width: 95%;
        padding: 8px;
    }

    .video-modal-close {
        top: -40px;
        font-size: 1.8rem;
        width: 36px;
        height: 36px;
    }

    .video-modal-content {
        width: 95%;
    }

    .video-close-btn {
        top: -35px;
        font-size: 2rem;
    }

    .text-section {
        padding: 50px 0;
        font-size: 1rem;
    }

    .text-section > * {
        max-width: 90%;
        padding: 0 15px;
    }

    .text-section h2 {
        font-size: 1.4rem;
    }

    .image-break {
        height: 45vh;
        background-attachment: scroll;
    }

    .video-section {
        padding: 50px 15px;
    }

    .video-section h2 {
        font-size: 1.6rem;
    }

    .video-section p {
        font-size: 1rem;
    }
}

/* Mobile nhỏ */
@media (max-width: 480px) {
    .hero-section {
        padding: 40px 10px;
    }

    .hero-overlay {
        padding: 25px 15px;
        width: 95%;
    }

    .hero-overlay h1 {
        font-size: 1.8rem;
        letter-spacing: 1px;
        margin-bottom: 10px;
    }

    .hero-overlay h2 {
        font-size: 0.95rem;
        margin-bottom: 18px;
    }

    .video-btn {
        padding: 10px 24px;
        font-size: 0.9rem;
    }

    .video-modal-box {
        width: 98%;
        padding: 5px;
        border-radius: 10px;
    }

    .video-modal-box video {
        border-radius: 8px;
    }

    .video-modal-close {
        top: -35px;
        font-size: 1.6rem;
        width: 32px;
        height: 32px;
    }

    .video-close-btn {
        top: -30px;
        font-size: 1.8rem;
    }

    .text-section {
        padding: 40px 0;
        font-size: 0.9rem;
    }

    .text-section > * {
        padding: 0 10px;
    }

    .text-section h2 {
        font-size: 1.2rem;
    }

    .image-break {
        height: 35vh;
    }

    .video-section {
        padding: 40px 10px;
    }

    .video-section h2 {
        font-size: 1.4rem;
    }

    .video-section p {
        font-size: 0.9rem;
        margin-bottom: 20px;
    }
}

